const aiTalkApplication = (productName: string, characterName: string, path: string, nickName: string): TApplicationTemplate => ({
  name: productName,
  bin: `C:\\Program Files (x86)\\${path}`,
  file: `${nickName}_*.wav`,
  folder: "%TEMPDIR%",
  modifiers: ['aiTalkRuby'],
  enableAsas: true,
  characterTemplates: [{
    onlyOne: true,
    name: characterName,
    editableName: false,
    nickName: nickName,
    file: `${nickName}_*.wav`,
    editableFile: false,
    dir: "%TEMPDIR%",
    editableDir: false,
    preset: "",
    usePreset: false,
    modifiers: ['aiTalkRuby'],
  }],
  stepperSetUp: <StepperSetUpButtonAITalk title={productName} />,
});
const voiceroidApplication = (productName: string, characterName: string, path: string, nickName: string): TApplicationTemplate =>
  aiTalkApplication('VOICEROID＋ '+productName, characterName, 'AHS\\VOICEROID+\\' + path + '\\VOICEROID.exe', nickName);
