import { Box, Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/cevio_menu.png';
import TalkImage from './images/cevio_talk.png';
import Talk2Image from './images/cevio_talk2.png';
import Menu2Image from './images/cevio_menu2.png';
import SaveImage from './images/cevio_save.png';
import StepperDialogButton from './StepperDialogButton';

const getSteps = (title: string): [string, React.ReactNode][] => [
  [
    `${title} を起動する`,
    <>
      <Typography>
        {title} を起動します。
      </Typography>
      <Box my={1}>
        <Typography variant="body2">
          ※もし既に {title} をかんしくんの連動起動により起動している場合は一旦閉じ、手動で起動し直してください。<br />
          この手順を守らなかった場合は　手順５　からやり直しになります。
        </Typography>
      </Box>
    </>,
  ],
  [
    '「オプション」ダイアログを開く',
    <>
      <img src={MenuImage} alt="メインメニューの選択例" width="226" height="81" />
      <Typography>
        エディターのメインメニューから［ツール(T)］→［オプション(O)...］を選びます。
      </Typography>
    </>,
  ],
  [
    '「トーク設定」タブを設定する',
    <>
      <img src={TalkImage} alt="設定例" width="697" height="415" />
      <Typography>
        「トーク設定」タブを開き、上記画像の赤枠のように設定します。
      </Typography>
      <ol>
        <li>「テキストファイルの文字コード」枠内にある「書き出し」を「Shift JIS」に設定する</li>
      </ol>
    </>,
  ],
  [
    '「オプション」ダイアログを閉じる',
    <>
      <Typography>
        「OK」ボタンを押して、「オプション」ダイアログを閉じます。
      </Typography>
    </>,
  ],
  [
    'トークボイスを選択し、適当なセリフを入力する',
    <>
      <img src={Talk2Image} alt="入力例" width="522" height="336" />
      <Typography>
        内容は何でもいいので適当なキャラクターで適当なセリフを入力します。
      </Typography>
    </>,
  ],
  [
    '「WAV書き出し」を選択する',
    <>
      <img src={Menu2Image} alt="選択例" width="613" height="587" />
      <Typography>
        セリフを右クリックし、メニューから「WAV書き出し(W)...」を選択します。
      </Typography>
    </>,
  ],
  [
    '「保存」ダイアログを設定する',
    <>
      <img src={SaveImage} alt="設定例" width="611" height="407" />
      <Typography>
        「保存」ダイアログ下部にある「セリフをテキスト出力」にチェックをつけ、「キャンセル」ボタンを押して「保存」ダイアログを閉じます。
      </Typography>
      <Box my={1}>
        <Typography variant="body2">
          ※もし「保存」のダイアログが表示されない場合は、{title} がかんしくんの連動起動を経由して起動しているのが原因の可能性があります。<br />
          その場合は一旦 {title} を閉じて、手動で起動し直した上で　手順５　からやり直してください。
        </Typography>
      </Box>
    </>,
  ],
];

export default function StepperSetUpButtonCeVIO(props: {
  title: string;
}) {
  const { title } = props;
  const steps = React.useMemo(() => getSteps(title), [title]);
  return (
    <StepperDialogButton caption="アプリケーション側の設定" title={`${title} 側の設定`} steps={steps} />
  );
}
