import React, { useState, useCallback, useMemo, useReducer } from 'react';
import { Container, Grid } from '@material-ui/core';
import AddApplicationButton from './AddApplicationButton';
import './App.css';
import { Application, TActions as TApplicationActions } from './Application';
import SwapAnimator from './SwapAnimator';
import SettingDialogButton from './SettingDialogButton';
import { TApplication, TApplicationTemplate } from './Types';
import { modifiers } from './Templates';
let gidCounter = 0;

type AppsAction = { type: 'add', v: TApplicationTemplate } | TApplicationActions;
const AppsReducer: React.Reducer<TApplication[], AppsAction> = (state, a) => {
  switch (a.type) {
    case 'add':
      const app: TApplication = { ...a.v, gkey: ++gidCounter, characters: [] };
      if (a.v.characterTemplates.length === 1 && a.v.characterTemplates[0].onlyOne) {
        app.characters.push({ ...a.v.characterTemplates[0], layer: 1, gkey: ++gidCounter });
      }
      delete (app as any).characterTemplates;
      delete (app as any).stepperSetUp;
      return [...state, app];
    case 'move-up':
      {
        const r = [...state];
        [r[a.index], r[a.index - 1]] = [r[a.index - 1], r[a.index]];
        return r;
      }
    case 'move-down':
      {
        const r = [...state];
        [r[a.index], r[a.index + 1]] = [r[a.index + 1], r[a.index]];
        return r;
      }
    case 'delete':
      {
        const r = [...state];
        r.splice(a.index, 1);
        return r;
      }
    case 'update':
      {
        const r = [...state];
        r[a.index] = { ...r[a.index], ...a.v };
        return r;
      }
    case 'add-character':
      {
        const r = [...state];
        r[a.index] = { ...r[a.index], characters: [...r[a.index].characters, { ...a.v, layer: 1, gkey: ++gidCounter }] };
        return r;
      }
    case 'character':
      {
        const r = [...state];
        const aa = a.action;
        const c = r[a.index].characters = [...r[a.index].characters];
        switch (aa.type) {
          case 'move-up':
            [c[aa.index], c[aa.index - 1]] = [c[aa.index - 1], c[aa.index]];
            break;
          case 'move-down':
            [c[aa.index], c[aa.index + 1]] = [c[aa.index + 1], c[aa.index]];
            break;
          case 'delete':
            c.splice(aa.index, 1);
            break;
          case 'update':
            c[aa.index] = { ...c[aa.index], ...aa.v };
            break;
        }
        r[a.index] = { ...r[a.index], characters: c };
        return r;
      }
  }
}

function toBinary(string: string): string {
  const codeUnits = new Uint16Array(string.length);
  for (let i = 0; i < codeUnits.length; i++) {
    codeUnits[i] = string.charCodeAt(i);
  }
  return String.fromCharCode(...(new Uint8Array(codeUnits.buffer) as any));
}

function exportTOML(apps: TApplication[]) {
  const r: string[] = [];
  r.push(`padding = 200`);
  r.push(`filemove = 'move'`);
  r.push(`deletetext = true`);
  r.push(``);

  r.push(`# ==== [[asas]] セクション ====`);
  r.push(``);

  for (const a of apps) {
    r.push(`# 「${a.name}」の連動起動`);
    r.push(`[[asas]]`);
    r.push(`exe = '''${a.bin}'''`);
    if (a.enableAsas) {
      if (a.file) {
        r.push(`format = '''${a.file}'''`);
      }
    } else {
      r.push(`flags = 0`);
    }
    r.push(``);
  }

  r.push(`# ==== [[rule]] セクション ====`);
  r.push(``);

  for (const a of apps) {
    for (const c of a.characters) {
      r.push(`# 「${c.name}」のためのルール`);
      r.push(`[[rule]]`);
      r.push(`layer = ${c.layer}`);
      r.push(`encoding = 'sjis'`);
      r.push(`file = '''${c.file}'''`);
      if (c.usePreset) {
        r.push(`text = '''^${c.preset.replace(/[.*+?^${}()|[\]\\]/g, '\\$&')}(?:| - .+?)＞'''`);
      }
      r.push(`modifier = '''`);
      for (const m of c.modifiers) {
        const mm = modifiers[m];
        r.push(`  -- ${mm[0]}`);
        r.push(`  ${mm[1]}`);
      }
      r.push(`  -- ファイル名を整形`);
      r.push(`  filename = os.date("%y%m%d_%H%M%S") .. [==[_${c.nickName}_]==] .. tofilename(text, 10) .. ".wav"`);
      r.push(`'''`);
      r.push(``);
    }
  }

  r.push(`# 以下の行は消さないでください`);
  r.push(`# forcepserconfig:`+btoa(toBinary(JSON.stringify(apps))));
  return r.join('\n');
}

const App = () => {
  const [apps, updateApps] = useReducer(AppsReducer, []);
  const [swappingKeys, setSwappingKeys] = useState<string[]>([]);
  const onClearSwapping = useCallback(() => setSwappingKeys([]), [setSwappingKeys]);
  const toml = useMemo(() => exportTOML(apps), [apps]);
  const onAddApplication = useCallback((v: TApplicationTemplate) => updateApps({ type: 'add', v }), [updateApps]);
  return (
    <Container>
      <h1>かんしくん設定ファイル作成ツール　テスト版</h1>
      <SwapAnimator onStart={setSwappingKeys} onEnd={onClearSwapping}>
        {apps.map((app, index) => (
          <Application
            ref={React.createRef()}
            key={app.gkey}
            app={app}
            index={index}
            length={apps.length}
            swapping={swappingKeys.includes(`${app.gkey}`)}
            dispatch={updateApps}
          />
        ))}
      </SwapAnimator>
      <Grid container direction="row" justify="space-between" alignItems="flex-start">
        <Grid item>
          <AddApplicationButton onAdd={onAddApplication} />
        </Grid>
        <Grid item>
          <SettingDialogButton content={toml} />
        </Grid>
      </Grid>
    </Container>
  );
}

export default App;
