import React, { useState, useMemo } from 'react';
import { FormControl, InputLabel, InputBaseComponentProps, OutlinedInput, InputAdornment, IconButton, Tooltip } from '@material-ui/core';

let gidCounter = 0;

function TextFieldButton(props: {
  size?: 'small' | 'medium';
  label: string;
  tooltip: string;
  inputProps?: InputBaseComponentProps;
  icon: React.ReactNode,
  buttonPosition?: 'start' | 'end';
  disabled?: boolean;
  value?: string;
  onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
  onChange?: (e: React.ChangeEvent<HTMLInputElement>) => void;
}) {
  const [gid] = useState(() => `textfield-button-${++gidCounter}`);
  const { size, label, tooltip, inputProps, icon, buttonPosition, disabled, value, onClick, onChange } = props;
  const pos = useMemo(() => buttonPosition === 'end' ? 'end' : 'start', [buttonPosition]);
  const button = useMemo(() => (
    <InputAdornment position={pos}>
      <Tooltip title={tooltip}>
        <IconButton aria-label={tooltip} onClick={onClick}>
          {icon}
        </IconButton>
      </Tooltip>
    </InputAdornment>
  ), [pos, tooltip, icon, onClick]);
  const adornmentPos = useMemo(() => ({ [`${pos}Adornment`]: button }), [pos, button]);
  return (
    <FormControl fullWidth variant="outlined" size={size} disabled={disabled}>
      <InputLabel htmlFor={gid}>{label}</InputLabel>
      <OutlinedInput
        id={gid}
        {...adornmentPos}
        label={label}
        value={value}
        onChange={onChange}
        inputProps={inputProps}
      />
    </FormControl>
  )
}
export default TextFieldButton;
